@import "../endpoints.scss";
.header_nav{
    align-items: center;
    display: flex;
    justify-content: space-between;
   /* margin: auto;*/
   font-size: 1.5em;
   height: 5.5em;

   @include tablet-only {
    font-size: 1em;
}
@include mobile-only {
font-size: 1em;
}
}
.title{
  margin-top: -1.5em;
}
.title h1{
    font-family: 'zeyada', cursive;
    font-size: 2em;
    @include mobile-only {
    font-size: 2em;
    }
}
.title p{
    margin: -2.5em 0px 0 4.5em;
    @include mobile-only {
      font-size: 1em;
      margin: -2.5em 0px 0 1em;
    }
   
}
.flexLink{
    align-items: center;
    display: flex;
    /*margin-left: 24em;*/
    @include mobile-only {
    position: fixed;
		top: -100vh;
		left: 7em;
    width: 100%;
    flex-direction: column;
		transition: 1s;
    } 
}
.responsive_nav {
@include mobile-only {
  transform: translateY(100vh);
}}
.nav-close-btn {
@include mobile-only {
  position: absolute;
  top: 2rem;
  right: 2rem;
}}
.navbar{
    margin-left: 1em;
    font-weight: 300;
    &:hover{
     font-weight: 100;
    }
    p{
      cursor: pointer;
     /* @include mobile-only {
      font-size: 1em;
    margin-top: -0.5em;}*/
}
}
.nav-btn {
	background: transparent;
	border: none;
	outline: none;
	color: #eee;
	visibility: hidden;
	opacity: 0;
  @include mobile-only {
      visibility: visible;
      opacity: 1;
      color: black;
    }
	}

.slider {
  position: relative;
  height: 2px;
  background-color: #ccc;
  border-radius: 4px;
  transition: background-color 0.3s ease-in-out;
  opacity: 0; /* Par défaut, la barre de progression est masquée */
}

.navbar:hover .slider {
  opacity: 1;
   /* La barre de progression devient visible lors du survol du lien */
}

.slider:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: grey;
  border-radius: 4px;
  transition: width 0.3s ease-in-out;
}
.navbar:hover .slider:before {
  width: 100%; /* La barre de progression s'étend sur toute la largeur lors du survol */
}
.burger-menu {
  height: 9%;
  width: 8em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  cursor: pointer;
  position: fixed;
  top: 2em;
  right: -5em;
}

.burger-bar {
  width: 2em;
  height: 0.25em;
  margin-bottom: 0.4em;
  background-color: black;
 display: none;
  border-radius: 0.5em;
  @include mobile-only {
display: inline-block;
}}

.menu {
  @include mobile-only {
  width: 50%;
  height: 100vh;
  background-color: rgb(154, 181,240);
  position: absolute;
  top: 0;
  z-index: -1;
}}

/* ------------- sliding menu ------------------ */
.hidden {
  @include mobile-only {
  display: none;
}}

.visible {
  @include mobile-only {
  display: inherit;
}}

/* ----- animations--------- */
/* clicked */
.burger-bar.clicked:nth-child(1){
  @include mobile-only {
  transform: rotate(45deg) translate(0.75em, 1.25em);
  transition: ease-out 0.5s;
}}

.burger-bar.clicked:nth-child(2){
  @include mobile-only {
  transform: scale(0.1);
  transition: ease-out 0.5s;
}}

.burger-bar.clicked:nth-child(3){
  @include mobile-only {
  transform: rotate(135deg) translate(-0.5em, 1em);
  transition: ease-out 0.5s;
}}

/* unclicked */
.burger-bar.unclicked {
  @include mobile-only {
  transform: rotate(0) translate(0);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
}}